//HOOKS
import useRace from "modules/army/hooks/useRace";
//STYLES
import { Container, Label, Select, Option } from "./styles/RaceSelector.styled";

function RaceSelector() {
  const { hangeRace, isMonstersShow } = useRace();

  return (
    <Container>
      <Label></Label>
      <Select
        id="race"
        onChange={e => {
          hangeRace(e.target.value);
        }}
      >
        <Option value="undead"></Option>
        <Option value="demon"></Option>
        <Option value="drow"> </Option>
        <Option value="human"></Option>
        <Option value="elf"></Option>
        {isMonstersShow ? <Option value="monsters"></Option> : null}
      </Select>
    </Container>
  );
}

export default RaceSelector;
